import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';


class BankAccountModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Bank Account
                        </Modal.Title>
                    </Modal.Header>

                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="BankAccountName">
                                <Form.Label>Bank Account Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter Bank Account Name" value={this.props.BankAccountName} onChange={this.props.changeBankAccountName} required />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a Bank name.
                                 </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group controlId="BankAccountCode">
                                <Form.Label>Bank Account Code</Form.Label>
                                <Form.Control type="text" placeholder="Enter Bank Account Code" value={this.props.BankAccountCode} onChange={this.props.changeBankAccountCode} required />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a Bank Code.
                                 </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group controlId="SystemStatusType">
                                <Form.Label>System Status Type</Form.Label>
                                <InputGroup>
                                   
                                    <Form.Control
                                        type="text"
                                        value={this.props.SystemStatusTypeDescription}
                                        placeholder="Search System Status Type"
                                        aria-describedby="inputGroupPrepend"
                                        required
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                    <Form.Control.Feedback type="invalid">
                                        Please select status.
                                 </Form.Control.Feedback>
                                </InputGroup>

                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Bank
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.BankAccountName}  readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default BankAccountModal;